function TeamCard({ name, url, ...other }) {
  return(
    <Card sx={{ width: '100%' }} {...other}>
      <CardActionArea sx={{ height:'100%' }}>
        <CardMedia
          component="img"
          image={url}
          height="300px"
          alt={name}
        // sx={{ height:'50% !important' }}
        />
        <CardContent sx={{ height:'60px' }}>
          <Typography gutterBottom variant="body1" component="div" sx={{ m:'0' }}>
            {name}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}

